/*------------------*
# Weather Module
*------------------*/
.the-day {
	margin: 0px auto;
	display: inline-block;
	h1 {
		font-size: 102px;
		line-height: 138px;
		color: white;
		&:after {
			content: "";
			width: 1px;
			height: 116px;
			display: inline-block;
			transform: rotate(-45deg);
			background-color: #aeafb3;
			position: relative;
			top: 45px;
			left: 50px;
		}
	}
	.para-1 {
		margin-left: 190px;
		margin-bottom: 0px;
		font-size: 32px;
		line-height: 43px;
		color: black;
	}
}
.weather-conditions {
	.col {
		text-align: center;
		color: $gray-500;
		& > i[class^="icon-"] {
			font-size: 83px;
			margin-bottom: 23px;
			display: inline-flex;
		}
		& > p {
			margin-bottom: 0px;
		}
		.wind-values {
			margin-top: 76px;
			display: flex;
			align-items: center;
			justify-content: center;
			div {
				display: inline-flex;
				align-items: center;
				margin-right: 30px;
				&:last-child {
					margin-right: 0px;
				}
			}
			i[class^="icon-"] {
				font-size: 40px;
				display: inline-flex;
				margin-right: 12px;
			}
			span {
				font-size: 18px;
				line-height: 24px;
			}
		}
		.temp-value {
			margin-top: 44px;
			h1 {
				font-size: 74px;
				line-height: 100px;
				font-weight: 500;
				position: relative;
				sup {
					font-size: 40px;
					position: absolute;
					top: 35px;
				}
			}
		}
		.rain-value {
			margin-top: 75px;
			display: flex;
			align-items: center;
			justify-content: center;
			i {
				font-size: 44px;
				display: inline-flex;
			}
			span {
				font-size: 18px;
				line-height: 24px;
			}
		}
	}
}

.temp-by-time {
	width: 100%;
	align-items: center;
	margin-top: 54px;
	margin-bottom: 68px;
	li {
		display: flex;
		align-items: center;
		flex-direction: column;

		a {
			padding-bottom: 20px;
			border-bottom: 1px solid $gray-200;
			color: $gray-550;
			position: relative;

			i[class^="icon-"] {
				font-size: 40px;
			}
			&:hover,
			&:focus {
				color: $gray-550;
			}
		}
		span {
			color: $gray-500;
			font-size: 18px;
			line-height: 24px;
			font-weight: 500;
			display: flex;
			justify-content: center;
			margin-top: 18px;
			opacity: 0;
			visibility: hidden;
		}
		&.active {
			margin-bottom: 34px;
			a {
				i[class^="icon-"] {
					font-size: 76px;
				}
				&::after {
					content: "";
					width: 100%;
					height: 5px;
					background-color: $gray-200;
					position: absolute;
					bottom: -3px;
					left: 0;
				}
			}
			span {
				opacity: 1;
				visibility: visible;
			}
		}
	}
}
.week-days {
	li {
		a {
			padding: 10px 27px;
			background-color: #cfced3;
			color: white;
			border-right: 1px solid white;

			&.active {
				background-color: #c4c4c4;
			}
			&:hover,
			&:focus {
				color: white;
			}
		}
		&:last-child {
			a {
				border-right: 0px;
			}
		}
	}
}
