/*---------------------------*
# Details Modal with tab
*---------------------------*/
.details-modal-tab {
	position: fixed;
	top: 50%;
	right: 0;
	transform: translate(110%, -50%);
	width: 100%;
	max-width: 1263px;
	height: auto;
	min-height: 100vh;
	z-index: z("details-modal");
	background-color: white;
	transition: transform 0.4s ease-in;
	&.show {
		transform: translate(0%, -50%);
		transition: transform 0.4s ease-out;
	}
	.tab-bar {
		position: absolute;
		left: 33%;
		top: 0;
		width: 67%;

		.nav-pills {
			background-color: #fcfcfc;
			flex-grow: 1;
			justify-content: center;
			.nav-link {
				padding: 20px 14px 20px 14px;
				position: relative;
				border-radius: 0px;
				color: $primary;
				text-transform: uppercase;
				font-size: 14px;
				line-height: 19px;

				&:after {
					content: "";
					display: block;
					width: 100%;
					height: 4px;
					background-color: transparent;
					position: absolute;
					left: 0;
					bottom: 0px;
					border-radius: 5px;
				}
				&.active {
					background-color: transparent;
					font-weight: 600;
					&:after {
						background-color: $primary;
					}
				}
			}
		}
		.d-flex {
			flex-shrink: 0;
			background-color: white;
			width: 87px;
		}
	}
	.details {
		.tab-pane {
			&.active {
				display: flex;
			}
		}
		.details-image {
			width: 33%;
			flex-shrink: 0;
			position: relative;
			overflow: hidden;
			img {
				width: auto;
				transform: translateX(-50%);
				position: relative;
				height: 100%;
				left: 50%;
			}
			.caption {
				width: 100%;
				height: 240px;
				background: linear-gradient(rgba(50, 50, 50, 0.1) 0%, rgba(0, 0, 0, 0.8) 100%);
				position: absolute;
				left: 0;
				bottom: 0;
				display: flex;
				align-items: flex-end;
				padding: 36px 35px 30px 35px;
				p {
					color: white;
					font-size: 14px;
					line-height: 19px;
					font-weight: 300;
					margin-bottom: 0px;
				}
			}
		}
		.details-content {
			margin: 88px 40px 50px 35px;
			width: 67%;
		}
	}
}
.rating-avg {
	display: flex;
	align-items: center;
	margin-bottom: 28px;
	h1 {
		font-weight: bold;
		font-size: 43px;
		line-height: 58px;
		margin-right: 21px;
		color: $primary;
		margin-bottom: 0px;
	}
	strong {
		font-weight: bold;
		font-size: 14px;
		line-height: 19px;
		color: black;
		margin-bottom: 5px;
	}
	.icon-star {
		font-size: 13px;
	}
	span {
		font-size: 12px;
		line-height: 16px;
		font-weight: 500;
		color: black;
	}
}
.rating-details {
	.d-flex {
		margin-bottom: 10px;
		align-items: center;
		span {
			font-size: 12px;
			line-height: 16px;
		}
	}
}
.details-modal-tab-backdrop {
	position: fixed;
	top: 0;
	right: 0;
	transform: translate(100%, 0);
	width: 100%;
	height: 100%;
	background-color: rgba(0, 0, 0, 0.8);
	z-index: z("details-modal-backdrop");
	opacity: 0;
	transition: opacity 0.5s ease-in;
	&.show {
		transform: translate(0%, 0%);
		opacity: 1;
		transition: opacity 0.5s ease-out;
	}
}
