/*------------------*
# Award Blocks
*------------------*/
.award-section {
	min-height: calc(100vh - 216px);
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
}
.awards {
	display: grid;
	grid-template-columns: 1fr 1fr 1fr;
	justify-items: center;
	.award-blocks {
		width: 314px;
		height: 482px;
		padding: 24px 23px 19px 24px;
		display: flex;
		flex-direction: column;
		align-items: flex-start;
		justify-content: center;
		transition: 0.3s;
		border-radius: 2px;
		cursor: pointer;
		background-color: $gray-200;
		&:hover {
			box-shadow: 0px 0px 10px rgba(123, 123, 123, 0.1);
			transition: 0.3s;
		}
		.award-block-texts {
			margin-top: auto;
			.title {
				font-size: 26px;
				line-height: 35px;
				margin-bottom: 6px;
			}
			a {
				margin-top: 21px;
				font-size: 12px;
				line-height: 16px;
				color: $primary;
				i {
					vertical-align: middle;
					font-size: 10px;
					margin-left: 3px;
					display: inline-flex;
				}
			}
		}
		&:nth-child(2) {
			background-color: $primary;
			color: white;
			.award-block-texts {
				.title {
					color: white;
				}
				a {
					color: white;
				}
			}
			&:hover {
				background-color: $primary-light;
			}
		}
	}
	@include media-breakpoint-down(lg) {
		grid-template-columns: 1fr;
	}
}
.reviews {
	display: flex;
	align-items: center;
	img {
		margin-right: 22px;
		width: 114px;
	}
	p {
		margin-bottom: 0px;
		font-size: 12px;
		font-weight: 300;
		line-height: 16px;
		.icon-star {
			color: $gold;
		}
	}
}
