/*------------------*
# Floating Menu
*------------------*/
.floating-menu-bar {
	position: fixed;
	right: 0;
	margin-right: 24px;
	top: 50%;
	transform: translate(150%, -50%);
	width: 76px;
	background-color: white;
	display: flex;
	flex-direction: column;
	transition: transform 0.8s ease-in-out;
	z-index: z("floating-menu");
	&.show {
		transform: translate(0, -50%);
		transition: transform 0.8s ease-in-out;
	}
	.burger {
		width: 100%;
		height: 74px;
		border-bottom: 1px solid #e5e5e5;
		display: flex;
		align-items: center;
		justify-content: center;
		margin-bottom: 28px;
		a {
			color: $primary;
		}
	}
	.separator {
		height: 111px;
		width: 1px;
		background-color: #bbbbbb;
		position: relative;
		margin: 0 auto;
		&::before {
			content: "";
			width: 1px;
			height: 50%;
			position: absolute;
			bottom: 0px;
			left: 0px;
			display: inline-block;
			background-color: $primary;
		}
	}
	.logo {
		height: 175px;
		width: 41px;
		margin-top: 36px;
		margin-bottom: 40px;
		a {
			display: inline-block;
			transform: rotate(-90deg);
			transform-origin: 96px 76px;
			width: 175px;
			img {
				width: 175px;
			}
		}
	}
	.btn.book-now {
		background-color: $primary;
		color: white;
		font-weight: 500;
		font-size: 15px;
		line-height: 17px;
		display: flex;
		align-items: center;
		justify-content: center;
		border-top: 1px solid #e5e5e5;
		border-bottom: 1px solid #e5e5e5;
		height: 76px;
	}
	.play {
		padding: 8px 7px 15px 7px;
		margin-top: 40px;
		font-size: 10px;
		line-height: 9px;
		font-weight: 600;
		display: flex;
		align-items: center;
		.icon-play {
			font-size: 14px;
			margin-right: 22px;
		}
	}
}
