/*------------------*
# Book Now Modal
*------------------*/
.book-now-modal {
	position: fixed;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	transform: translateX(100%);
	z-index: 0;
	background-color: white;
	transition: z-index 0s 1s, transform 1.2s cubic-bezier(0.68, -0.6, 0.32, 1.6);
	&.show {
		z-index: z("book-now-mod");
		transform: translateX(0);
		transition: transform 1.2s ease-in-out;
	}
	&.bounceFromRight {
		z-index: z("book-now-mod");
		transform: translateX(0);
		transition: transform 1.2s cubic-bezier(0.68, -0.6, 0.32, 1.6);
	}
	.close-modal {
		position: absolute;
		right: 55px;
		top: 43px;
		i.icon-cross {
			font-size: 22px;
		}
	}
	.left-column {
		padding: 0 51px 0 58px;
		height: 100%;
		display: flex;
		flex-direction: column;
		justify-content: center;
		background-color: $gray-200;
		position: relative;
		padding-bottom: 200px;
		&::after {
			content: "";
			width: 1px;
			height: 385px;
			background-color: $b9b0;
			display: inline-block;
			position: absolute;
			top: calc(50% + 20px);
			right: -30px;
			transform: translateY(-50%) rotate(-45deg);
		}
	}
	.right-column {
		height: 100%;
		display: flex;
		flex-direction: column;
		justify-content: center;
		align-items: center;
	}
}
.book-now-menu {
	display: flex;
	flex-direction: column;
	a {
		color: $c757;
		padding-left: 20px;
		border-left: 3px solid transparent;
		&.active {
			color: $primary;
			border-left: 3px solid $primary;
		}
	}
}
.book-now-form {
	.form-group {
		display: flex;
		flex-direction: column;
		text-align: center;
		margin-bottom: 54px;
		& > i {
			font-size: 24px;
			margin-bottom: 10px;
		}
	}
	label {
		@extend .para-1;
		font-weight: 400;
		color: black;
		margin-bottom: 10px;
	}

	.tvaf-input {
		border: 0px solid transparent;
		border-bottom: 1px solid #cfced3;
		font-size: 34px;
		line-height: 46px;
		padding: 3px 5px;
		text-align: center;
		color: $gray-500;
		&::placeholder {
			color: $gray-500;
		}
		&:focus,
		&:active {
			border: 0px solid transparent;
			border-bottom: 1px solid #cfced3;
			outline: 0;
		}
	}
	button {
		border: 0px solid transparent;
		background-color: transparent;
		font-size: 40px;
		line-height: 54px;
		padding: 0px;
		display: flex;
		align-items: center;
		margin: auto;
		.circle-35 {
			border: 0.5px solid #000000;
			display: flex;
			align-items: center;
			justify-content: center;
			margin-left: 20px;
			margin-top: 5px;
			i {
				font-size: 20px;
			}
		}
	}
}
