/*------------------*
# Footer
*------------------*/
.footer-top {
	position: relative;
	display: flex;
	background-color: $gray-200;
	.sitemap {
		width: 25%;
		display: flex;
		flex-direction: column;
		justify-content: center;
		align-items: center;
		background-color: $primary;
		h2 {
			font-size: 42px;
			line-height: 57px;
			font-weight: 300;
			color: white;
			margin-bottom: 14px;
		}
		a.para-1 {
			color: white;
			margin-bottom: 25px;
		}
		.circle-35 {
			background-color: white;
			.icon-arrow-right {
				color: $gray-550;
			}
		}
	}
	.social-icons {
		display: flex;
		flex-direction: column;
		justify-content: center;
		padding: 0 25px;
		a {
			color: $gray-600;
			font-size: 28px;
		}
	}
	.contact-details {
		padding: 64px 33px 64px 38px;
		address {
			font-size: 16px;
			line-height: 22px;
			font-weight: 300;
		}
	}
	.block {
		width: 20%;
		height: 100%;
		position: absolute;
		right: 0;
		top: 0;
		background-color: white;
		img {
			position: absolute;
			bottom: 90px;
			left: 0px;
		}
	}
	.the-v {
		position: absolute;
		right: calc(20% - 1px);
		top: 0%;
		height: 100%;
		img {
			height: 100%;
		}
	}
}
.footer-bottom {
	background-color: $gray-200;
	padding: 19px 0;
	p {
		font-size: 13px;
		color: $gray-500;
		line-height: 18px;
		margin-bottom: 0px;
	}
}

.back-to-top {
	background-color: $primary;
	color: white;
	position: fixed;
	right: 72px;
	bottom: 10px;
	opacity: 0;
	visibility: hidden;
	z-index: 0;
	transition: opacity 0.5s, z-index 0s 0.5s, visibility 0s 0.5s;
	&:hover {
		color: white;
	}
	&.show {
		opacity: 1;
		visibility: visible;
		z-index: 1;
		transition: z-index 0s 0s, visibility 0s 0s, opacity 0.5s;
	}
}
