/*------------------*
# Titles
*------------------*/

.section-title-1 {
	font-weight: 100;
	font-size: 52px;
	line-height: 55px;
	b {
		font-weight: 700;
	}
}
.section-title-2 {
	font-size: 40px;
	font-weight: 400;
	line-height: 54px;
}
.section-title-3 {
	font-weight: 500;
	font-size: 54px;
	line-height: 73px;
}
.section-title-4 {
	font-weight: 500;
	font-size: 50px;
	line-height: 68px;
}
.title {
	font-weight: 400;
	font-size: 30px;
	line-height: 41px;
	color: $primary;
}
.title-2 {
	font-size: 24px;
	font-weight: 400;
	line-height: 32px;
}
.subtitle-1 {
	font-weight: normal;
	font-size: 32px;
	line-height: 43px;
	margin-bottom: 0px;
}
.subtitle-2 {
	font-size: 34px;
	line-height: 46px;
	font-weight: 300;
	b {
		font-weight: 600;
	}
}
.para-1 {
	font-size: 20px;
	line-height: 27px;
	font-weight: 300;
}
.para-2 {
	font-size: 29px;
	line-height: 39px;
	font-weight: 300;
}
.para-3 {
	font-size: 12px;
	line-height: 16px;
	font-weight: 400;
}
.display-1 {
	font-size: 102px;
	line-height: 138px;
}

.cursor-pointer {
	cursor: pointer;
}
.fw-l {
	font-weight: 300;
}
.fw-r {
	font-weight: 400;
}
.fw-m {
	font-weight: 500;
}

.styled-list {
	list-style-type: none;
	padding-left: 20px;
	li {
		margin-bottom: 14px;
		position: relative;
		color: black;
		&:last-child {
			margin-bottom: 0px;
		}
		&:before {
			content: "\e90d";
			font-family: "icomoon";
			display: inline-block;
			position: absolute;
			left: -20px;
			top: 0;
			font-size: 11px;
			line-height: 24px;
			z-index: 1;
		}
	}
}
