/*------------------*
# main cover
*------------------*/
.main-cover {
	// min-height: 790px;
	height: 100vh;
	position: relative;
	background: url("../images/main-cover-bg.jpg") no-repeat 0 0 / cover;
	.cover-container {
		position: relative;
		padding-left: 155px;
		z-index: 2;
	}
	.cta {
		font-size: 42px;
		line-height: 42px;
		i.icon-play {
			font-size: 28px;
			margin-left: 15px;
		}
	}
	.cover-text {
		display: flex;
		justify-content: center;
		flex-direction: column;
		margin-top: -140px;
	}
	.logo-book-now {
		display: flex;
		flex-direction: column;
		align-items: flex-start;
		position: relative;
		top: 44px;
		.hamburger {
			position: absolute;
			left: -50px;
			top: 10px;
			color: white;
			i {
				font-size: 22px;
			}
		}
		.logo {
			margin-bottom: 15px;
		}
		.btn-outline-primary {
			background-color: white;
			padding: 8px 33px;
			font-weight: 600;
			&:hover {
				color: $primary;
			}
		}
	}
	.video-bg {
		position: absolute;
		left: 0%;
		top: 0%;
		z-index: 0;
		width: 100%;
		height: 100%;
		pointer-events: none;
		overflow: hidden;
		iframe {
			width: 100vw;
			height: 56.25vw; /* Given a 16:9 aspect ratio, 9/16*100 = 56.25 */
			min-height: 100vh;
			min-width: 177.77vh; /* Given a 16:9 aspect ratio, 16/9*100 = 177.77 */
			position: absolute;
			top: 0%;
			left: 0%;
		}
		&:after {
			content: "";
			width: 100%;
			height: 100%;
			background-color: rgba(0, 0, 0, 0.5);
			position: absolute;
			left: 0;
			right: 0;
		}
	}
	.cover-side-panel {
		position: absolute;
		right: 0px;
		top: 0px;
		height: 100%;
		background: white;
		width: 310px;
		// &::after {
		// 	content: "";
		// 	position: absolute;
		// 	border-right: 190px solid white;
		// 	border-top: 100vh solid transparent;
		// 	right: 100%;
		// 	top: 0px;
		// }
		&.v2 {
			width: 0px;
			background-color: transparent;
		}
	}
	.the-v {
		position: absolute;
		top: 0;
		right: 309px;
		height: 100%;
		img {
			height: 100%;
		}
		z-index: 1;
	}
	.v2 {
		.the-v {
			right: 0px;
		}
	}
	.block {
		position: absolute;
		top: 0;
		right: 0;
		display: flex;
		flex-direction: column;
		height: 100%;
		width: 100%;
		padding-left: 20px;
		padding-right: 26px;
		padding-top: 40px;
		z-index: 2;
		.top-buttons {
			display: flex;
			align-items: center;
			.icon-grid {
				font-size: 22px;
				margin-left: auto;
				cursor: pointer;
				color: $primary;
			}
		}
		.tagline {
			display: inline-block;
			margin-left: auto;
			margin-bottom: 110px;
			h4 {
				font-size: 16px;
				text-transform: uppercase;
				border-bottom: 0.5px solid $primary;
				text-align: center;
				padding-bottom: 14px;
				width: 65%;
				margin: auto;
				margin-bottom: 5px;
			}
		}
		.logo {
			width: 234px;
			margin-left: auto;
			margin-bottom: 38px;
		}
	}
	.nav-link {
		text-align: right;
		font-size: 16px;
		line-height: 22px;
		position: relative;
		color: $primary;
		text-transform: uppercase;
		&:before {
			content: "";
			display: inline-block;
			position: absolute;
			width: 27px;
			height: 1px;
			background-color: #cfced3;
			top: -23px;
			right: 0px;
		}
		&:after {
			content: "";
			width: 4px;
			height: 11px;
			background: $primary;
			position: absolute;
			right: 0px;
			top: 15px;
		}
	}
	.scroll-down {
		margin-bottom: 35px;
		text-align: right;
		.icon-mouse-scroll {
			font-size: 46px;
			color: $primary;
		}
	}
	.block-v2 {
		position: absolute;
		right: 26px;
		bottom: 0px;
		width: 150px;
		z-index: 1;
		.nav-link {
			color: white;
			margin-bottom: 44px;

			&:after {
				background: white;
			}
		}
		.scroll-down {
			.icon-mouse-scroll {
				color: white;
			}
		}
	}
	.services {
		position: absolute;
		left: 0px;
		width: 11px;
		bottom: 52px;
		display: flex;
		z-index: 1;
		.service-blocks {
			display: flex;
			position: relative;
			left: -145px;
			top: 0;
			transform: translateX(-100%);
			transition: all 1.2s cubic-bezier(0.19, 1, 0.22, 1);

			a {
				background-color: white;
				width: 265px;
				height: 167px;
				margin-right: 17px;
				border-radius: 4px;
				overflow: hidden;
				&:last-child {
					margin-right: 0px;
				}
				p {
					text-align: center;
					font-size: 12px;
					line-height: 16px;
					font-weight: 600;
					margin: 10px auto;
					text-transform: uppercase;
				}
			}
		}
		.bar {
			height: 167px;
			width: 11px;
			background: white;
			z-index: 2;
			flex-shrink: 0;
			cursor: pointer;
			// &:hover {
			// 	& ~ .service-blocks {
			// 		left: 155px;
			// 		transform: translateX(0);
			// 		transition: all 0.5s;
			// 	}
			// }
		}
		&.show {
			width: auto;
			.service-blocks {
				position: relative;
				left: 145px;
				transform: translateX(0);
				transition: all 1.2s cubic-bezier(0.68, -0.55, 0.265, 1.55);
			}
		}
	}
}
