/*------------------*
# main menu
*------------------*/
.body {
	&.bounceToLeft {
		transform: translateX(-100%);
		transition: transform 1.2s cubic-bezier(0.68, -0.6, 0.32, 1.6);
	}
}
.main-menu {
	width: 100%;
	height: 100%;
	position: fixed;
	top: 0;
	left: 0;
	display: flex;
	transform: translateX(100%);
	z-index: 0;
	transition: z-index 0s 1s, transform 1.2s cubic-bezier(0.68, -0.6, 0.32, 1.6);
	&.show {
		z-index: z("main-menu");
		transform: translateX(0);
		transition: transform 1.2s ease-in-out;
	}
	&.hideToLeft {
		transform: translateX(-100%);
		z-index: 0;
		transition: z-index 0s 1.2s, transform 1.2s cubic-bezier(0.68, -0.6, 0.32, 1.6);
	}
	&.bounceFromRight {
		z-index: z("main-menu");
		transform: translateX(0);
		transition: transform 1.2s cubic-bezier(0.68, -0.6, 0.32, 1.6);
	}
	.menu-columns {
		width: 21%;
		height: 100%;
		background-color: $gray-200;
		border-right: 1px solid white;
		padding-left: 25px;
		padding-right: 21px;
		position: relative;
		h1 {
			font-size: 136px;
			line-height: 184px;
			color: white;
			margin-bottom: 215px;
		}
		a.d-flex {
			align-items: center;
			font-size: 20px;
			line-height: 27px;
			justify-content: space-between;
			.circle-35 {
				font-size: 15px;
				line-height: 1em;
			}
		}
		.sub-menu {
			width: 100%;
			height: 100%;
			background-color: white;
			padding-left: 25px;
			padding-right: 21px;
			position: absolute;
			top: 0px;
			left: 0px;
			transform: translateY(100%);
			transition: 0.6s cubic-bezier(0.11, 0, 0.5, 0);
			&.show {
				transform: translateY(0%);
				transition: 0.6s cubic-bezier(0.11, 0, 0.5, 0);
			}
			.d-flex {
				justify-content: space-between;
				margin-bottom: 120px;
				align-items: center;
				.circle-35 {
					background-color: $gray-100;
					margin-bottom: 50px;
					cursor: pointer;
				}
			}
			h1 {
				color: $gray-200;
				margin-bottom: 0px;
			}
			h4 {
				font-size: 20px;
				line-height: 27px;
				text-transform: uppercase;
				color: $primary;
			}
			ul {
				margin-left: 50px;
				li {
					a {
						display: block;
						padding: 20px 0;
						color: $gray-500;
						span {
							&.title {
								color: $gray-500;
								text-transform: uppercase;
								font-weight: 400;
								font-size: 16px;
								line-height: 22px;
							}
						}
						p {
							font-size: 14px;
							line-height: 19px;
							margin-bottom: 0;
						}
					}
				}
			}
		}
	}
	.menu-column-last {
		width: 16%;
		background: $gray-400;
		font-size: 16px;
		line-height: 22px;
		padding-top: 33px;
		.d-flex {
			padding-left: 33px;
			justify-content: space-between;
			padding-right: 28px;
			align-items: center;
			text-transform: uppercase;
			font-weight: 500;
			margin-bottom: 70px;
			i.icon-cross {
				font-size: 22px;
				cursor: pointer;
			}
		}
		ul {
			margin-left: 33px;
			margin-bottom: 50px;
			li {
				a {
					display: block;
					padding: 8px 0;
					color: white;
					&:hover {
						color: $primary;
					}
				}
			}
		}
		.copyright-text {
			font-size: 11px;
			line-height: 15px;
			padding-left: 22px;
			padding-right: 12px;
		}
	}
}
.rooms-suits-menu {
	width: 100%;
	height: 100%;
	background-color: white;
	position: fixed;
	top: 0;
	left: 0;
	display: flex;
	transform: translateX(100%);
	z-index: 0;
	transition: z-index 0s 1.2s, transform 1.2s cubic-bezier(0.68, -0.6, 0.32, 1.6);
	&.show {
		z-index: z("property-menu");
		transform: translateX(0);
		transition: transform 1.2s cubic-bezier(0.68, -0.6, 0.32, 1.6);
	}
	.description-column {
		border-left: 8px solid $gray-600;
		padding-left: 43px;
		padding-right: 32px;
	}
}
.menu-2-header-item-1 {
	padding-top: 17px;
	padding-bottom: 14px;
	height: 61px;
	.logo {
		width: 130px;
		margin-right: 46px;
	}
	.icon-grid {
		font-size: 22px;
		padding: 2px 5px;
	}
}
.menu-2-header-item-2 {
	display: flex;
	.bevel-bottom-right {
		height: 61px;
		position: relative;
		width: 70%;
		display: flex;
		align-items: center;
		justify-content: center;
		&:after {
			content: "";
			border-bottom: 61px solid $gray-500;
			border-right: 71px solid transparent;
			position: absolute;
			right: -71px;
		}
	}
	.circle-35 {
		background-color: $gray-550;
		.icon-arrow-right {
			font-size: 19px;
			color: white;
		}
	}
}
.menu2-slider-item {
	border-right: 1px solid white;
	.position-relative {
		position: relative;
	}
	.info {
		width: 0px;
		height: 0px;
		opacity: 0;
		visibility: hidden;
		position: absolute;
		top: 0;
		left: 0;
		z-index: -1;
		background-color: rgba($gray-400, 0.8);
		transition: opacity 0.6s;
		display: flex;
		flex-direction: column;
		justify-content: center;
		padding-left: 80px;
		& > div {
			margin-bottom: 30px;
		}
		h1 {
			color: white;
			font-size: 58px;
			line-height: 78px;
			sup {
				position: relative;
				font-size: 18px;
				line-height: 24px;
			}
		}
		h4 {
			font-size: 18px;
			line-height: 24px;
			text-transform: uppercase;
		}
		.d-flex {
			color: white;
			span {
				color: white;
				font-size: 18px;
				line-height: 24px;
			}
			span.sep {
				margin: 0 10px;
			}
		}
	}
	&:hover {
		.info {
			width: 100%;
			height: 100%;
			opacity: 1;
			visibility: visible;
			z-index: 2;
			transition: opacity 0.6s;
		}
	}
	.captions {
		background-color: $gray-200;
		padding: 20px 24px;
		.title {
			text-transform: uppercase;
		}
		.desc {
			text-transform: lowercase;
			color: black;
		}
		.separator {
			height: 1px;
			width: 75%;
			background-color: white;
			position: relative;
			&:before {
				content: "";
				width: 26px;
				height: 1px;
				background-color: $gray-500;
				position: absolute;
				left: 0;
				top: 0;
			}
		}
	}
}
