/*------------------*
# Mixins
*------------------*/
$z-indexes: (
	"main-menu": 1050,
	"book-now-mod": 1045,
	"property-menu": 1040,
	"details-modal": 1040,
	"details-modal-backdrop": 1030,
	"floating-menu": 1000,
);

// function for z-index

@function z($val) {
	@if not map-has-key($z-indexes, $val) {
		@warn "No Layer found for '#{$val}' in $z-layers map. Property omitted.";
	}

	@return map-get($z-indexes, $val);
}
