/*------------------*
# Variables
*------------------*/

/*
 * This file includes default variables to get going quickly on a project. 
*/

/*
 * Base font family
*/

$font-family-unineue: "Uni Neue", Arial, sans-serif !default;
$font-family-base: $font-family-unineue;
$font-family-unineu-book: "Uni Neue Book", Arial, sans-serif !default;
$body-bg: #f2f2f2;

/*
 * Base colors
*/
// scss-docs-start theme-color-variables

$falu-red: #89181a;
$gold: #f4b459;
$black: #000;

// scss-docs-start color-variables
$gray-100: #f6f6f7;
$gray-200: #eaeaec;
$gray-400: #d4d4d8;
$gray-550: #aeafb3;
$gray-500: #9c9c9c;
$gray-600: #b1b1b7;
$gray-900: #212529;
$blue: #0d6efd;
$yellow: #ffc107;
$green: #198754;
$cyan: #0dcaf0;
$dada: #dadada;
$b9b0: #b9b0b3;
// scss-docs-end color-variables

// scss-docs-start theme-color-variables
$primary: #50562b;
$primary-light: #7c8546;
$secondary: $gray-600;
$success: $green;
$info: $cyan;
$warning: $yellow;
$danger: $falu-red;
$light: $gray-200;
$dark: $gray-900;
$ccc: #cccccc;
$f4f4: #f4f4f4;
$c757: #757575;
// scss-docs-end theme-color-variables

// scss-docs-start theme-colors-map
$theme-colors: (
	"primary": $primary,
	"secondary": $secondary,
	"success": $success,
	"info": $info,
	"warning": $warning,
	"danger": $danger,
	"light": $light,
	"dark": $dark,
);
// scss-docs-end theme-colors-map

$new-colors: (
	"ccc": $ccc,
	"gray": $gray-500,
	"gray-2": $gray-200,
	"f4f4": $f4f4,
	"primary-light": $primary-light,
	"c757": $c757,
);

$theme-colors: map-merge($theme-colors, $new-colors);
// scss-docs-start theme-colors-map

$body-color: #444444;
$link-color: $black;
$link-decoration: none;
// $link-shade-percentage: 20% !default;
// $link-hover-color: shift-color($link-color, $link-shade-percentage) !default;
// $link-hover-decoration: null !default;

$line-height-base: 1.375;

// scss-docs-start spacer-variables-maps
$spacer: 1rem;
$spacers: (
	0: 0,
	1: $spacer / 4,
	2: $spacer / 2,
	3: $spacer,
	4: $spacer * 1.5,
	5: $spacer * 3,
	6: $spacer * 6,
	20: 20px,
	30: 30px,
	35: 35px,
	40: 40px,
	45: 45px,
	50: 50px,
	55: 55px,
	70: 70px,
	75: 75px,
	80: 80px,
	90: 90px,
	120: 120px,
);

// scss-docs-start input-btn-variables
$input-btn-padding-y: 13px;
$input-btn-padding-x: 18px;
$input-btn-font-size: 14px;

$input-btn-focus-width: 0px;
$input-btn-focus-color: none;
$input-btn-focus-box-shadow: none;

$input-color: #616163;
$input-border-color: #000000;
$input-border-radius: 2px;

// Buttons
//
// For each of Bootstrap's buttons, define text, background, and border color.

// scss-docs-start btn-variables
$btn-padding-y: 2px;
$btn-padding-x: 18px;
$btn-font-size: 19px;
$btn-line-height: 26px;
$btn-font-weight: 300;

$btn-focus-width: 0;
$btn-box-shadow: none;
$btn-focus-box-shadow: none;
$btn-active-box-shadow: none;

$btn-border-radius: 1px;
